.App {
  text-align: center;
}

a {
  cursor: pointer;
  text-decoration: none;
}
.main-section, .detail-page {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.section-header {
  font-size: calc(32px + 2vmin);
  text-align: center;
  padding-top: 64px;
}

/* DETAIL PAGES */
.detail-column {
  /* max-width: 400px; */
}

.detail-page {
  background-color: white;
  color: #686868;
  padding-bottom: 36px;
}

.detail-row {
  margin: 0 auto;
  /* margin-top: 72px;
  margin-bottom: 72px; */
  text-align: center;
  max-width: 800px;
}

.detail-image {
  max-width:100%;
  max-height:100%;
  padding-bottom: 8px;
  /* width: 350px; */
}

.detail-image-left, .detail-image-right {
  max-width:100%;
  max-height:100%;
  padding-bottom: 8px;
  /* width: 350px; */
}

.detail-image-left {
  float: right;
}

.detail-image-right {
  float: left;
}

.detail-image-tight {
  max-width:100%;
  max-height:100%;
  padding: 0;
  /* width: 250px; */
}

.detail-row {
  margin-top: 72px;
  margin-bottom: 64px;
}

.detail-hero-image {
  width:90%;
  height: auto;
  padding: 12px;
}
